
@media only screen and (max-width: 480px) {
    /* Your styles for mobile go here */
    .app{
      display: flex;
      flex-direction: column-reverse;
    }
    .sh{
      
    }

    header{
      flex-direction: column;
      align-items: baseline;
      height: 6.5vh;
    }
    .app{
      margin-top: 6.5vh;
    }
    .hicon{
      height: 5vh;
      width: 5vh;
    }
    .slogan{
      display: none;
    }
    .sec1{
      padding-top: 2vh;
      margin-top: 2vh;
      padding-left: 2vh;
    }
    .featured-details {
      padding-left:3vh ;
    }
    .song-search-input{
      padding-left:3vh ;
    }
    .customize-header {
      /* padding-left:3vh ; */
    }
    .option-title {
      padding-left: 1vh;
    }
    .option-list {
      padding-left: 1vh;
    }
    .sec2{
      display: flex;
      align-items: center;
      flex-direction: column;
  
    }
    .sec2div{
      margin-top:2vh;
      background-color: black;
      color: aliceblue;
      border-radius: 10px;
      width: 80%;
      display: flex;
      align-items: center;
      flex-direction: column;
    }
    .s2d1{
      /* background-color: antiquewhite; */
      background-color: rgba(255, 255, 255, 0.229);
      border-radius: 40px;
      width: 23vh;
      height: 23vh;
    }
    .s2d0{
      display: flex;
      width: 100%;
      padding: 1.5vh;
      min-height: max-content;
    }
    .s2d2{
      display: flex;
      justify-content: center;
      align-items: center;
      /* gap: 5%; */
      height: 5vh;
      width: 90%;
      margin-top:2vh;
      margin-bottom: 2vh;
      border-radius: 20px;
    }
    .s2d2btn{
      height: 25px !important;
      width: 25px !important;
    }
    .playbtn {
      color: rgb(169, 169, 169);
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.14);
      border: none;
      outline: none;
      transition: 0.3s ease;
    }
    
    
    .playicon{
      height: 40px;
      width: 40px;
    }
    .bot{
      padding-top: 2vh;
      padding-bottom: 1vh;
      display: flex !important;
      justify-content: center;
      width: 100%;
    }
    .bott{
      font-size: 1.2em;
      font-family: 'Sora', sans-serif;
      background-color: black;
  
    }
    .sbot{
      display: none;
    }
    .dlogan{
      background-color: black;
      display: flex;
      height: 8vh;
      align-items: flex-end;
      padding-bottom: 1vh;
      padding-right: 3vh;
      justify-content: center;
    }
    .dlogantext{
      font-family: 'Dancing Script', cursive; /* Cursive font for a handwritten feel */
      font-weight: 400; /* Regular weight to maintain elegance */
      color: #ffffffb2; /* Dark color for readability */
      text-align: center;
      font-size: 1.5em;
      letter-spacing: 0.5px; /* Slight spacing between letters */
    }
    .slogan{
      display: none;
    }
    .get-button-container {
      margin-top: 20px;
      padding-left: 1vh;
    }
    .footer{
      /* margin-top: 5vh; */
      background-color: rgb(0, 0, 0);
      color: white;
      bottom: 0;
      font-size: 0.9em;
    }
    .footerlap {
      display: none;
    }

  }

